import { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './Cryptosearch.css';

function CryptoSearch() {
  const { coinName } = useParams();
  const navigate = useNavigate();
  const [coin, setCoin] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchCoin = async () => {
      try {
        setLoading(true);
        const response = await axios.get(
          `https://api.coingecko.com/api/v3/coins/markets`,
          {
            params: {
              vs_currency: 'usd',
              ids: coinName.toLowerCase(),
              order: 'market_cap_desc',
              per_page: 1,
              page: 1,
              sparkline: false,
            },
          }
        );
        setCoin(response.data[0]);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
        setCoin(null);
        setLoading(false);
      }
    };
    fetchCoin();
  }, [coinName]);

  return (
    <div className="crypto-search-container">
      <button onClick={() => navigate('/cryptolist')} className="back-button">
        Back to List
      </button>

      {loading ? (
        <p>Loading...</p>
      ) : coin ? (
        <div className="coin-card">
          <img src={coin.image} alt={coin.name} className="coin-logo" />
          <h2>
            {coin.name} ({coin.symbol.toUpperCase()})
          </h2>
          <p>Current Price: ${coin.current_price.toLocaleString()}</p>
          <p>Market Cap: ${coin.market_cap.toLocaleString()}</p>
          <p>24h Price Change: {coin.price_change_percentage_24h.toFixed(2)}%</p>
        </div>
      ) : (
        <p>No results found for "{coinName}". Please try another name.</p>
      )}
    </div>
  );
}

export default CryptoSearch;
